<template>
	<div>
		<div @click="$router.back()">whhhhhh</div>
		<van-grid :column-num="3">
			<van-grid-item v-for="(value,index) in categories_male" :key="index">
				<h3>{{value.name}}</h3>
				<p>{{value.bookCount}}</p>
			</van-grid-item>
		</van-grid>
		<van-grid :column-num="3">
			<van-grid-item v-for="(value,index) in categories_female" :key="index">
				<h3>{{value.name}}</h3>
				<p>{{value.bookCount}}</p>
			</van-grid-item>
		</van-grid>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				categories_female: null,
				categories_male: null
			}
		},
		created() {
			this.axios.get('http://novel.kele8.cn/categories').then((response) => {
				console.log(response);
				this.categories_female = response.data.female;
				this.categories_male = response.data.male;
			})
		}
	}
</script>

